import Events from '../Events';

const Cards = (props) => {

    return <div className="App-Cards">
        {props.CardArray.map((option, i) => {
            return <div key={i} 
                className="Card"
                onClick={() => {
                    props.SetAppState({ Loading: true });
                    Events.client.request({
                        Oauth2: props.Oauth2,
                        Page: option.value,
                        Property: props.State,
                        Value: i,
                        SetAppState: props.SetAppState
                    });
                }}
            >
                <a className="Card-Title">{option.label}</a>
            </div>
        })}
    </div>
}

export default Cards;